.toggle {
    width: 50px;
    height: 25px;

    border-radius: 20px;
    border: 1px solid thistle;
    background-color: white;
    
    position: fixed;
    top: 10px;
    right: 10px;
    
    z-index: 999;
    
    display: flex;
    align-items: center;
    justify-content: space-around;

    cursor: pointer;
}

.toggle-icon {
    width: 15px;
    height: 15px;
}

.toggle-button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: gray;
    opacity: 0.5;
    z-index: 999;
    position: absolute;
}
